	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Raining on my sleep","date":"22 Jul 2003","slug":"/2003-07-22-raining-on-my-sleep/","tags":"Y2003"},"html":"<p>I still insist that 'hot' begins at 40°C, but the last couple of days have been 'very warm'. All that energy in the atmosphere has just been discharged in the form of a thunderstorm that chased us from our bedroom in the attic. Drizzle pattering on the skylights might be romantic, but a downpour certainly is not.</p>\n<p>We've booked our flight and hotel (burning up some of Mama's Lufthansa miles) for our anniversary getaway to Rome. Lilli <a href=\"/2003/07/13/2419.php\">asked</a> what we're interested in seeing there. Well, we have a cheap guidebook, so we know what the main attractions are. Mama is a militant non-Cathlolic, so we don't need to see the Vatican. We mostly want to stay outside, explore, and catch some atmosphere. And we'd appreciate any tips like 'If you're in Rome for a weekend, you've just got to do <i>this</i>'.</p>"},"next":{"fields":{"title":"One of my goals is to make (the program) so informal that calling it a program is laughable.","date":"22 Jul 2003","slug":"/2003-07-22-one-of-my-goals-is-to-make-the-program-so-informal-that-calling-it-a-program-is-laughable/"}},"previous":{"fields":{"title":"Smash your television, trash your DVD player, get a life!","date":"22 Jul 2003","slug":"/2003-07-22-smash-your-television-trash-your-dvd-player-get-a-life/"}}},{"node":{"fields":{"title":"One of my goals is to make (the program) so informal that calling it a program is laughable.","date":"22 Jul 2003","slug":"/2003-07-22-one-of-my-goals-is-to-make-the-program-so-informal-that-calling-it-a-program-is-laughable/","tags":"Y2003"},"html":"<p><a title=\"View from an Iowa Homestead: Waterslide\" href=\"http://iowa.weblogger.com/2003/07/21\">One of my goals is to make (the program) so informal that calling it a program is laughable.</a></p>"},"next":{"fields":{"title":"European Baseball: Continental Divide Despite Some Promising Signs","date":"22 Jul 2003","slug":"/2003-07-22-european-baseball-continental-divide-despite-some-promising-signs/"}},"previous":{"fields":{"title":"Raining on my sleep","date":"22 Jul 2003","slug":"/2003-07-22-raining-on-my-sleep/"}}},{"node":{"fields":{"title":"European Baseball: Continental Divide Despite Some Promising Signs","date":"22 Jul 2003","slug":"/2003-07-22-european-baseball-continental-divide-despite-some-promising-signs/","tags":"Y2003"},"html":"<p><a title=\"'We've come a long way. Teams get their own fields now instead of playing on soccer fields.'\" href=\"http://www.nytimes.com/2003/07/19/sports/baseball/19EURO.html?ex=1374033600&en=d8c489cfde52b199&ei=5007&partner=USERLAND\">European Baseball: Continental Divide Despite Some Promising Signs</a></p>"},"next":{"fields":{"title":"Be careful what you wish for","date":"22 Jul 2003","slug":"/2003-07-22-be-careful-what-you-wish-for/"}},"previous":{"fields":{"title":"One of my goals is to make (the program) so informal that calling it a program is laughable.","date":"22 Jul 2003","slug":"/2003-07-22-one-of-my-goals-is-to-make-the-program-so-informal-that-calling-it-a-program-is-laughable/"}}}],"pathPrefix":"","first":false,"last":false,"index":673,"pageCount":1101}}

